import React from 'react';
import { motion } from 'framer-motion';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { animations } from '../../constants/animations';

// Styled Components
const Container = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
`;

const AnimationContainer = styled(motion.div)`
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5% 10%;
`;

const Endpoint = styled(motion.div)`
  width: 25%;
  aspect-ratio: 1 / 1;
  background-image: linear-gradient(to bottom right, #f0f9ff, rgba(219, 234, 254, 0.5));
  border-radius: 0.75rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px solid #bfdbfe;
  position: relative;
`;

const EndpointIcon = styled.svg`
  width: 33.333%;
  height: 33.333%;
  color: #3b82f6;
`;

const EndpointLabel = styled.span`
  margin-top: 0.5rem;
  font-size: 0.875rem;
  color: #2563eb;
  font-weight: 500;
`;

const ArrowContainer = styled.div`
  position: absolute;
  width: 100%;
`;

const RequestContainer = styled(ArrowContainer)`
  top: 40%;
`;

const ResponseContainer = styled(ArrowContainer)`
  top: 60%;
`;

const RequestLabel = styled(motion.div)`
  position: absolute;
  background-color: #dbeafe;
  padding: 0.25rem 0.5rem;
  border-radius: 0.5rem;
  color: #2563eb;
  font-size: 0.75rem;
  white-space: nowrap;
  z-index: 10;
  left: 43%;
  transform: translateY(-50%);
  text-align: center;
`;

const ResponseLabel = styled(motion.div)`
  position: absolute;
  background-color: #d1fae5;
  padding: 0.25rem 0.5rem;
  border-radius: 0.5rem;
  color: #059669;
  font-size: 0.75rem;
  white-space: nowrap;
  z-index: 10;
  right: 62%;
  transform: translateY(-50%);
  text-align: center;
`;

const RequestLine = styled(motion.div)`
  position: absolute;
  height: 0.125rem;
  background-color: #3b82f6;
  left: 20%;
  width: 39%;
  transform-origin: left center;
`;

const ResponseLine = styled(motion.div)`
  position: absolute;
  height: 0.125rem;
  background-color: #10b981;
  left: 19%;
  width: 41%;
  transform-origin: right center;
`;

const ArrowHead = styled(motion.div)`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
`;

const RequestArrowHead = styled(ArrowHead)`
  right: -1%;
`;

const ResponseArrowHead = styled(ArrowHead)`
  left: -1%;
`;

const ArrowHeadInner = styled.div`
  width: 0.75rem;
  height: 0.75rem;
  transform: rotate(45deg);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
`;

const RequestArrowHeadInner = styled(ArrowHeadInner)`
  background-color: #3b82f6;
`;

const ResponseArrowHeadInner = styled(ArrowHeadInner)`
  background-color: #10b981;
`;

interface HttpRequestAnimationProps {
  isVisible: boolean;
}

const HttpRequestAnimation: React.FC<HttpRequestAnimationProps> = ({ isVisible }) => {
  const { t } = useTranslation();
  const timeline = animations.steps.httpRequest.timeline;

  return (
    <Container>
      <AnimationContainer
        initial={{ opacity: 0 }}
        animate={{ opacity: isVisible ? 1 : 0 }}
        transition={{ duration: timeline.containerFadeIn.duration }}
      >
        {/* Browser */}
        <Endpoint
          initial={{ opacity: 0, scale: 0.8 }}
          animate={isVisible ? { opacity: 1, scale: 1 } : { opacity: 0, scale: 0.8 }}
          transition={{ delay: timeline.browserServerDisplay.start, duration: timeline.browserServerDisplay.duration }}
        >
          <EndpointIcon fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
              d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9" />
          </EndpointIcon>
          <EndpointLabel>
            {t('animations.httpRequest.browser')}
          </EndpointLabel>
        </Endpoint>

        {/* Server */}
        <Endpoint
          initial={{ opacity: 0, scale: 0.8 }}
          animate={isVisible ? { opacity: 1, scale: 1 } : { opacity: 0, scale: 0.8 }}
          transition={{ delay: timeline.browserServerDisplay.start, duration: timeline.browserServerDisplay.duration }}
        >
          <EndpointIcon fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
              d="M5 12h14M5 12a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v4a2 2 0 01-2 2M5 12a2 2 0 00-2 2v4a2 2 0 002 2h14a2 2 0 002-2v-4a2 2 0 00-2-2m-2-4h.01M17 16h.01" />
          </EndpointIcon>
          <EndpointLabel>
            {t('animations.httpRequest.server')}
          </EndpointLabel>
        </Endpoint>

        {/* Request arrow */}
        <RequestContainer>
          <RequestLabel
            initial={{ opacity: 0 }}
            animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
            transition={{ delay: timeline.requestLine.start + 0.2 }}
          >
            {t('animations.httpRequest.request')}
          </RequestLabel>

          {/* Request line */}
          <RequestLine
            initial={{ scaleX: 0 }}
            animate={isVisible ? { scaleX: 1 } : { scaleX: 0 }}
            transition={{ delay: timeline.requestLine.start, duration: timeline.requestLine.duration }}
          >
            <RequestArrowHead
              initial={{ left: 0 }}
              animate={isVisible ? { left: '100%' } : { left: 0 }}
              transition={{
                delay: timeline.requestLine.start,
                duration: timeline.requestLine.duration,
                ease: "easeInOut"
              }}
            >
              <RequestArrowHeadInner />
            </RequestArrowHead>
          </RequestLine>
        </RequestContainer>

        {/* Response arrow */}
        <ResponseContainer>
          <ResponseLabel
            initial={{ opacity: 0 }}
            animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
            transition={{ delay: timeline.responseLine.start + 0.2 }}
          >
            {t('animations.httpRequest.response')}
          </ResponseLabel>
          
          {/* Response line */}
          <ResponseLine
            initial={{ scaleX: 0 }}
            animate={isVisible ? { scaleX: 1 } : { scaleX: 0 }}
            transition={{ delay: timeline.responseLine.start, duration: timeline.responseLine.duration }}
          >
            <ResponseArrowHead
              initial={{ right: '100%' }}
              animate={isVisible ? { right: 0 } : { right: '100%' }}
              transition={{
                delay: timeline.responseLine.start,
                duration: timeline.responseLine.duration,
                ease: "easeInOut"
              }}
            >
              <ResponseArrowHeadInner />
            </ResponseArrowHead>
          </ResponseLine>
        </ResponseContainer>
      </AnimationContainer>
    </Container>
  );
};

export default HttpRequestAnimation;
